<script>
    import LaunchApp from './LaunchApp.vue';
    import NoData from './NoData.vue';
    export default {
        props: {
            list: {
                type: Array,
                default: []
            },
            type: {
                type: String,
                default: 'space'
            },
            appParams: {
                type: String,
                default: ''
            }
        },
        components: {
            LaunchApp,
            NoData
        },
        filters: {
            formatTime:function (value) {
                return moment.utc(value).local().format('YYYY-MM-DD HH:mm');
            },
            ifNull:function (value) {
                if(!value){
                    return 0 ;
                }else{
                    return value;
                }
            }
        },
        mounted() {
            this.$util.initRate();
        },
        data() {
            return {
                appleCheck: false,
                cardWidth: (document.body.clientWidth - 30) / 2,
                message: {
                    en: {
                        "digital": "Digital",
                        "discontinue": "Discontinue",
                        "tweebucks": "T-Beans"
                    },
                    zh: {
                        "digital": "数字产品",
                        "discontinue": "下架",
                        "tweebucks": 'T豆'
                    }
                }
            }
        },
        methods: {

        }
    }
</script>
