import Tips from '../../components/Tips.vue';
import NoData from '../../components/NoData.vue';
import LaunchApp from '../../components/LaunchApp.vue';
import FooterDownload from '../../components/FooterDownload.vue';
import MomentList from '../../components/MomentList.vue';
import ProductList from '../../components/ProductList.vue';
import Loading from '../../components/Loading.vue';

var PAGE_SIZE = 5;

export default {
    name: 'Invite',
    components: {
        Tips,
        LaunchApp,
        FooterDownload,
        NoData,
        MomentList,
        ProductList,
        Loading
    },
    mounted() {

    },
    created() {
        this.getUserDetails();
        this.getMoments();
        this.getProducts();
        this.getViewTime();
        this.getTiv();
    },
    data() {
        return {
            userId: this.$util.getUrlParam('userId'),
            storeName: window.decodeURIComponent(this.$util.getUrlParam('storeName')),
            shareid: this.$util.getUrlParam('shareid'),
            userInfo: {
                BackgroundUrl: '',
                ThumbnailAvatarUrl: '',   // 头像
                Followed: '',
                FollowersCount: '--',
                FollowingCount: '--'
            },
            TiV: '--',
            viewTime: '--',

            paramsForApp: '',

            listBtn: 'list1',


            showLoadingMoreMoment: true,
            isLoadingMoment: true,
            mPage: 0,
            momentsData: [],

            showLoadingMoreProduct: true,
            isLoadingProduct: true,
            pPage: 0,
            productsData: [],
            prodPicWidth: 0,

            message: {
                en: {
                    tweebaa: 'Tweebaa',
                    "post": "Posts",
                    "products": "Products",
                    "update": "Updating...",
                    "digital": "Digital",
                    "discontinue": "Discontinue",
                    "follow": "Follow",
                    "followed": "Followed",
                    "follower": "Follower",
                    "following": "Following",
                    "chat": "Chat",
                    "views": "Views",
                    "loadingMore": "Click to view more"
                },
                zh: {
                    tweebaa: '推易吧',
                    "post":     "瞬间",
                    "products": "产品",
                    "update": "努力更新中...",
                    "digital": "数字产品",
                    "discontinue": "下架",
                    "follow": "关注",
                    "followed": "已关注",
                    "follower": "粉丝",
                    "following": "关注",
                    "chat": "互动",
                    "views": "浏览量",
                    "loadingMore": "点击查看更多"
                },
            }
        };
    },

    methods: {
        _pars(obj) {
            let results = [];
            for (let key in obj) {
                results.push(key + '=' + obj[key]);
            }
            return results.join('&');
        },
        changeList: function (list) {
            this.listBtn = list;
            if (list === 'list1') {
                this.refreshMoment();
            } else {

            }
        },
        refreshMoment() {
            this.mPage = 0;
            this.getMoments();
        },
        refreshProduct() {
            this.pPage = 0;
            this.getProduct();
        },
        getUserDetails() {
            var that = this;
            var url = this.$util.getBaseUrl() + "/api/users/" + this.userId;
            this.$util.getAxois(url, "GET", {}).then((response) => {
                that.userInfo = response.data;
                that.$refs.profileCard.style.backgroundImage = 'url(' + that.userInfo.BackgroundUrl + ')';

                that.paramsForApp = that._pars({
                    userid: that.userId,
                    storename: response.data.StoreName,
                    avatarurl: response.data.AvatarUrl
                });

            }).catch(function (error) {
                console.log(JSON.stringify(error) + '@@@@@@@@@@@@@@@@');
            });
        },
        getTiv() {
            var that = this;
            var url = this.$util.getBaseUrl() + "api/v2/tiv?UserId=" + this.userId;
            this.$util.getAxois(url, "GET", {}).then(function (response) {
                that.TiV = response.data.Data.CompletedTiv.toFixed(2);
            }).catch(function (error) {
                console.log(JSON.stringify(error));
            });
        },
        getViewTime() {
            var that = this;
            var url = this.$util.getBaseUrl() + "api/dashboard/v1/ty/" + this.userId;
            this.$util.getAxois(url, "GET", {}).then(function (response) {
                if (response.data.Code === 0) {
                    that.viewTime = response.data.Data.Views;
                }
            }).catch(function (error) {
                console.log(JSON.stringify(error) + '@@@@@@@@@@@@@@@@');

            });

        },
        getMoments() {
            var that = this;

            var url = this.$util.getBaseUrl() + "/api/v3/moments";
            var paramStr = {
                "ownerId": this.userId,
                "currentpage": this.mPage,
                "itemsperpage": PAGE_SIZE,
                "ShowOnHomePage": true,
                "viewerId": this.userId,
            };
            if (this.mPage === 0) {
                that.isLoadingMoment = true;
            }
            this.$util.getAxois(url, "POST", paramStr).then((response) => {
                let list = response.data.Data;
                if (list.length < PAGE_SIZE) {
                    this.showLoadingMoreMoment = false;
                }
                if (this.mPage === 0) {
                    that.isLoadingMoment = false;
                    that.momentsData = list;
                } else {
                    that.momentsData = that.momentsData.concat(list);
                }
            }).catch(function (error) {
                console.log(JSON.stringify(error) + '$$$$$$$$$$$$$$$$$$');
            });
        },
        getProducts() {
            var that = this;

            var url = this.$util.getBaseUrl() + "/api/v2/users/products?LanguageCode=" + this.$util.language;
            var paramStr = {
                "ownerId": this.userId,
                "currentpage": this.pPage,
                "itemsperpage": PAGE_SIZE + 1,
                "LanguageCode": this.$util.language
            };
            if (this.pPage === 0) {
                that.isLoadingProduct = true;
            }
            this.$util.getAxois(url, "POST", paramStr).then((response) => {
                let list = response.data.Data;
                if (list.length < PAGE_SIZE + 1) {
                    this.showLoadingMoreProduct = false;
                }
                if (this.pPage === 0) {
                    that.isLoadingProduct = false;
                    that.productsData = list;
                } else {
                    that.productsData = that.productsData.concat(list);
                }
            }).catch(function (error) {
                console.log(JSON.stringify(error) + '@@@@@@@@@@@@@@@@');
            });
        },
        loadingMoreMoment() {
            this.mPage++;
            this.getMoments();
        },
        loadingMoreProduct() {
            this.pPage++;
            this.getProducts();
        }
    }
}
